@import './variables';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: $ghostwhite;
}

.app__base {
    h1 {
        text-align: center;
        animation: zoom 1s 1s ease-in,
                   slide 1s ease-in;
        font-size: 40px;
    }

    @keyframes zoom {
        0% {
            transform: scale(1);
        } 50% {
            transform: scale(1.2);
        } 100% {
            transform: scale(1);
        }
    }

    @keyframes slide {
        from {
            transform: translateX(-1100px);
        } to {
            transform: translateX(50px);
        }
    }
}


.search__base {
    text-align: center;
    padding-top: 20px;

    input {
        width: 200px;
        height: 25px;
    }
}